.nav{
    z-index: 1; position: fixed;
    top: 50px; left: 10px;
    display: flex; flex-direction: column;
    background-color: rgba(255, 255, 255, 1); padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px  -5px rgb(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.295), 0 -6px 16px -6px rgba(0, 0, 0, 0.295);
}
@media screen and (max-width:1090px){
    .nav{
        left: initial; top: initial;
        bottom: 0; width: 100%;
    }   
}
.nav a{
    text-decoration: none;
    color: #0008fc; text-transform: uppercase;
    font-size: 12px; text-align: center; font-weight: 600;
}
.nav a:not(:last-child){
    margin-bottom: 20px;
}